export const StyledListItem = styled.div`
width: 100%;
    min-height: 90px;
    max-height: 90px;
    background: #fff;
    border: 1px solid #bbb;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
    display: flex;
    align-items: center;
    padding: 15px;
    `;

