import styled from "styled-components";

/**
 * @todo            container
 */
export const StyledContainer = styled.div`
`;

/**
 * @todo       button  form element
 */
export const StyledForm = styled.form`
    width: 100%;
    height: 120px;
    background: #fff;
    border-radius: 30px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
    padding: 30px;
    display: flex;
    align-items: center;
    border: 0.5px solid #bbb;
`;

/**
 * @todo      element
 */
export const StyledEnter = styled.div`
    flex: 1;
`

/**
 * @todo      element
 */
export const StyledLeave = styled.div`
    flex: 1;
`

/**
 * @todo      span element
 */
export const StyledTitle = styled.span`
    font-size: 38px;
`

/**
 * @todo      (input)     input element
 */
export const StyledInput = styled.input`
    width: 55px;
    height: 38px;
    font-size: 38px;
    border: none;
    background: transparent;
    text-align: center;

    &:focus {
        outline: none;
    }
    &::-webkit-inner-spin-button {
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
    }
`;

/**
 * @todo  
 */
export const StyledEnterButton = styled.button`
    width: 160px;
    height: 65px;
    font-size: 32px;
    background: #88ff00;
    border: 1px solid #bbb;
    border-radius: 20px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    font-family: bold;
    color: #fff;

    &:focus {
        outline: none;
    }
`;

/**
 * @todo    p element
 */
export const StyledText = styled.p`
    padding: 10px 30px;
    font-family: bold;
    color: #888;
    font-size: 22px;
`