import React, { useState } from "react";
import { addData } from "../apis/Room/addPeople";
import { useStateContext } from "../Context";
import { StyledContainer, StyledForm, StyledEnter, StyledTitle, StyledInput, StyledLeave, StyledEnterButton, StyledText } from "./style/content/main/enter/Enter.style"

/**
 * 
 * @returns     ,  button  element
 */
export const RoomEnter = () => {
    const {user,render, setRender} = useStateContext();
    const [enterH, setEnterH] = useState("");
    const [enterM, setEnterM] = useState("");
    const [leaveH, setLeaveH] = useState("");
    const [leaveM, setLeaveM] = useState("");

    const handleForm = async (e) => {
        e.preventDefault();

        if(window.localStorage.getItem("User") === null){
            alert(" ");
            return
        }
        if (enterH === "" || enterM === "" || leaveH === "" || leaveM === ""){
            alert("  ");
            return
        }
        if (window.localStorage.getItem("enter") !== "true"){
            const data = {
                user: user,
                time: {
                    enter: [parseInt(enterH) + 12, enterM],
                    leave: [parseInt(leaveH) + 12, leaveM]
                },
            }
            window.localStorage.setItem("enter", true);
            await addData(data);
            alert(".");
            setRender(!render);
        } else {
            alert("  .")
        }
        setEnterH("");
        setEnterM("");
        setLeaveH("");
        setLeaveM("");
    }

    return(
        <>
            <StyledContainer>
                <StyledForm onSubmit={handleForm}>
                    <StyledEnter>
                        <StyledTitle> [</StyledTitle>
                        <StyledInput 
                            type="number"
                            placeholder=""
                            value={enterH}
                            onChange={(e) => {
                                if(window.localStorage.getItem("User") === null){
                                    alert(" ");
                                    return
                                }
                                if (e.target.value > 9){
                                    e.target.value = "";
                                    alert("9    .")
                                    return
                                }
                                setEnterH(e.target.value)
                            }}
                        />
                        <StyledTitle>:</StyledTitle>
                        <StyledInput
                            type="number"
                            placeholder=""
                            value={enterM}
                            onChange={(e) => {
                                if(window.localStorage.getItem("User") === null){
                                    alert(" ");
                                    return
                                }
                                if (e.target.value > 60){
                                    alert(" 60   .")
                                    return
                                }
                                setEnterM(e.target.value)
                            }}
                        />
                        <StyledTitle>]</StyledTitle>
                    </StyledEnter>
                    <StyledLeave>
                    <StyledTitle> [</StyledTitle>
                        <StyledInput 
                            type="number"
                            placeholder=""
                            value={leaveH}
                            onChange={(e) => {
                                if(window.localStorage.getItem("User") === null){
                                    alert(" ");
                                    return
                                }
                                if (e.target.value > 9){
                                    e.target.value = "";
                                    alert("9    .")
                                    return
                                }
                                setLeaveH(e.target.value)
                            }}
                        />
                        <StyledTitle>:</StyledTitle>
                        <StyledInput
                            type="number"
                            placeholder=""
                            value={leaveM}
                            onChange={(e) => {
                                if(window.localStorage.getItem("User") === null){
                                    alert(" ");
                                    return
                                }
                                if (e.target.value > 60){
                                    alert(" 60   .")
                                    return
                                }
                                setLeaveM(e.target.value)
                            }}
                        />
                        <StyledTitle>]</StyledTitle>
                    </StyledLeave>
                    <StyledEnterButton></StyledEnterButton>
                </StyledForm>
                <StyledText>  .</StyledText>
            </StyledContainer>
        </>
    )
}

